<template>
  <v-container style="margin-top: 150px; margin-bottom: 150px; max-width: 800px;">
    <h1>{{ post.title.rendered }}</h1>

    <v-layout justify-center>
      <v-flex v-html="post.content.rendered" style="line-height: 1.8em;">
        <!-- {{ post.content.rendered }} -->
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      post: {}
    }
  },

  created () {
    this.$axios.get('/wordpress/wp-json/wp/v2/posts/' + this.$route.params.id).then((items) => {
      console.log(items.data)
      this.post = items.data
    })
  }
}
</script>

<style scoped lang="css">
  .wp-block-image img {
    width: 100%;
  }
</style>
